@import url('https://fonts.googleapis.com/css2?family=Prompt:wght@300;400;500;600;700&display=swap');

* {
  box-sizing: border-box;
}

body::-webkit-scrollbar {
  width: 0.5rem;
}

body::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

body::-webkit-scrollbar-thumb {
  background-color: #5b7fb1;
  outline: 1px solid slategrey;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #323232;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Alert */

.loading {
  display: block !important;
}
.success {
  color: #20a020;
}
.danger {
  color: #a02020;
}
.alert {
  padding: 1rem;
  border: 0.1rem solid transparent;
  border-radius: 0.5rem;
}
.alert-info {
  color: #2020a0;
  background-color: #e0e0ff;
}
.alert-danger {
  color: #a02020;
  background-color: #ffe0e0;
}
.alert-success {
  color: #20a020;
  background-color: #eeffe0;
}

/* Cart Badge */
.badge {
  background-color: #f02020;
  color: #ffffff;
  border-radius: 50%;
  padding: 0.4rem 0.5rem;
  font-size: 0.7rem;
  margin-left: 0.2rem;
}

/* Table */
.table {
  width: 100%;
  border-collapse: collapse;
}

.table tbody tr:nth-of-type(odd) {
  background-color: #f4f4f4;
}
.table td,
.table th {
  text-align: left;
  border: 0.1rem solid #e4e4e4;
  padding: 0.5rem;
}
.table button {
  margin: 0 0.2rem;
}

/* Proceed to Checkout */

.card {
  border: 0.1rem #c0c0c0 solid;
  background-color: #f8f8f8;
  border-radius: 0.5rem;
  margin: 1rem;
}
.card-body {
  padding: 1rem;
}
.card-body > * {
  margin-bottom: 0.5rem;
}

/* Crop Container */
.image-upload {
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  width: 60vw;
  height: 60vh;

  .image-cropper {
    // border: 1px solid black;
    width: inherit;
    height: inherit;
    // position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .crop-container {
      // border: 1px solid red;
      width: auto;
      height: inherit;
      position: relative;
      top: 0;
      left: 0;
      right: 0;
      bottom: 80px;

      .controls {
        // border: 1px solid green;
        position: absolute;
        bottom: 0;
        width: 100%;
        display: flex;
        justify-content: center;
      }
    }
  }

  .resized-image {
    border: 1px solid blue;
  }
}

/* StepsUI Component */

.steps-ui {
  display: flex;
  flex-direction: row;

  div {
    border-top: 0.3rem #c0c0c0 solid;
    color: #c0c0c0;
    flex: 1;
    padding: 1rem;
    font-weight: bold;
  }
}
.steps-ui {
  div.active {
    border-top-color: #f62d01;
    color: #f62d01;
  }
}

/* Error Page */
.errorGif {
  width: 50vw;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
}

/* dropdown */
.dropdown {
  display: inline-block;
  position: relative;

  .dropdown-content {
    position: absolute;
    display: none;
    right: 0;
    min-width: 8em;
    padding: 1rem;
    z-index: 1;
    background-color: #323232;
    list-style-type: none;
    margin: 0;
    margin-top: 0.4rem;
    border-radius: 0.5rem;

    .link {
      color: #ffffff;
      text-decoration: none;
    }
  }
}

.dropdown:hover .dropdown-content {
  display: block;
}

/* Signin Page */

.form {
  border: 1px solid black;
  background-color: #eeebeb;
  display: flex;
  justify-content: center;
  width: 30%;
  height: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);

  .form-container {
    // border: 1px solid blue;
    height: 45vh;
    width: 25vw;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;

    .form-heading {
      display: flex;
      justify-content: center;
      align-items: center;
      // border: 1px solid green;

      .logo {
        width: 3rem;
        height: 3rem;
      }

      span {
        margin-left: 1rem;
        font-weight: bold;
      }
    }

    .alert-box {
      // border: 1px solid purple;
      width: 20vw;
      height: 110px;
      margin-top: 2.5rem;
    }

    .form-fields {
      // border: 1px solid red;
      width: 20vw;

      li {
        list-style-type: none;
        margin: 0.5rem 0;
      }

      label {
        font-weight: 600;

        .link {
          font-size: 0.8rem;
          font-weight: 400;
          align-self: flex-end;
        }
      }

      input {
        width: 100%;
        height: 2.3rem;
        border-radius: 3px;
        border: 1px solid rgb(126, 126, 126);
        margin-top: 5px;
      }

      h3,
      label,
      input {
        display: flex;
        // border: 1px solid purple;
        justify-content: space-between;
      }

      button {
        width: 60%;
        height: 2rem;
        border: none;
        color: white;
        font-weight: 600;
        letter-spacing: 1px;
        border-radius: 3px;
        margin-top: 1rem;
      }

      button.primary {
        background-color: #6184c4;
      }
      button:hover {
        background-color: #a4bff1;
      }
    }
  }
}

/* Upload Modal */

/* Crop Container */
.image-upload {
  border: 1px solid black;
  display: flex;
  flex-direction: column;
  // width: 60vw;
  // height: 60vh;

  .image-cropper {
    // border: 1px solid black;
    width: inherit;
    height: inherit;
    // position: relative;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;

    .crop-container {
      // border: 1px solid red;
      width: auto;
      height: inherit;
      position: relative;
      top: 0;
      left: 0;
      right: 0;
      bottom: 80px;

      .controls {
        border: 1px solid green;
        position: absolute;
        bottom: 0;
        width: 100%;
        display: flex;
        justify-content: center;
      }
    }
  }

  .resized-image {
    border: 1px solid blue;
  }
}

.modal {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;

  .modal-content {
    position: relative;
    width: 50vw;
    height: 70vh;
    background-color: #fff;
    color: black;

    .upload-form {
      // border: 1px solid red;
      label {
        margin-left: 1rem;
        border-bottom: 2px solid blue;
      }
      label:hover {
        color: grey;
      }
    }

    .modal-header {
      padding: 10px;
      h4 {
        margin: 0;
      }
    }

    .modal-body {
      position: relative;
      // border: 1px solid blue;
      height: 50vh;
      padding: 10px;
      border-top: 1px solid #eee;
      border-bottom: 1px solid #eee;
    }

    .modal-footer {
      padding: 10px;
      // border: 1px solid blue;
      height: 12vh;
      background-color: #fff;

      .controls {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        .slider {
          width: 50%;
        }
      }

      button {
        margin-top: 1rem;
        margin-right: 1rem;
        background-color: #6184c4;
        border: none;
        border-radius: 3px;
        color: #fff;
        height: 1.7rem;
        font-size: 12px;
      }

      button:hover {
        background-color: #6184c4e9;
      }

      span {
        color: grey;
        font-size: 11px;
      }
    }
  }
}

/* Profile Page */

.profile-container {
  // border: 1px solid red;
  font-family: prompt;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: space-evenly;
  margin-top: 5rem;

  .sidebar {
    // border: 1px solid blue;
    width: 10vw;
    height: 60vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    font-weight: 600;
    text-decoration: none;

    ul {
      // border: 1px solid blue;
      list-style: none;
    }

    .selected {
      text-decoration: none;
    }
  }

  .main {
    // border: 1px solid red;
    padding: 3rem;
    color: white;
    border-radius: 2px;
    background-color: #323232;
    width: 40vw;
    overflow-y: auto;
    height: auto;

    .form-heading {
      // border: 1px solid blue;
      display: flex;
      justify-content: space-between;
      align-items: center;

      button {
        margin-top: 1rem;
        margin-right: 1rem;
        background-color: #6184c4;
        border: none;
        border-radius: 3px;
        color: #fff;
        height: 1.7rem;
        font-size: 12px;
      }
    }

    button:hover {
      background-color: #6184c4e9;
    }

    .form-fields {
      // border: 1px solid red;
      display: flex;
      flex-direction: column;

      button {
        margin-top: 1rem;
        margin-right: 1rem;
        background-color: #6184c4;
        border: none;
        border-radius: 3px;
        color: #fff;
        height: 1.7rem;
        font-size: 12px;
        width: 100%;
      }

      li {
        width: 100%;
        margin: 5px;
        display: flex;
        flex-direction: column;
        align-self: center;
        list-style: none;
      }

      input,
      textarea {
        height: 26px;
        border-radius: 3px;
      }
    }

    .profile-wrapper {
      // border: 1px solid green;
      display: flex;
      flex-direction: column;

      .profile-header {
        // border: 1px solid red;
        display: flex;
        flex-direction: column;
        align-items: center;

        .heading {
          // border: 1px solid red;
          display: flex;
          flex-direction: row;
          justify-content: space-evenly;
          width: 100%;

          .admin-badge {
            width: 80px;
            align-self: center;
          }
        }

        img {
          // border: 1px solid yellow;
          width: 150px;
          height: 150px;
          border-radius: 50%;
        }
      }
    }
  }

  .main::-webkit-scrollbar {
    width: 0.5rem;
  }

  .main::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  }

  .main::-webkit-scrollbar-thumb {
    background-color: #5b7fb1;
    outline: 1px solid slategrey;
  }
}

/* Laptop */
@media (min-width: 1250px) and (max-width: 1400px) {
  .form {
    width: 50vw;
    height: auto;

    .form-container {
      // border: 1px solid blue;
      height: 55vh;
      width: 40vw;

      .form-fields {
        // border: 1px solid red;
        width: 100%;

        input {
          width: 100%;
        }
      }
    }
  }
}

/* Tablet */
@media (min-width: 416px) and (max-width: 1024px) {
  .form {
    width: 60vw;
    height: auto;

    .form-container {
      // border: 1px solid blue;
      height: 55vh;
      width: 50vw;

      .form-fields {
        // border: 1px solid red;
        width: 100%;

        input {
          width: 100%;
        }
      }
    }
  }

  /* Profile Page */
  .profile-container {
    // border: 1px solid red;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;

    .sidebar {
      // border: 1px solid blue;
      width: 90vw;
      height: 5rem;
      flex-direction: row;
      justify-content: space-between;
    }

    .main {
      // border: 1px solid purple;
      padding: 2rem;
      margin-bottom: 2rem;
      width: 90vw;
      height: auto;
    }
  }
}

/* Mobile */
@media (min-width: 200px) and (max-width: 415px) {
  // Signin and register
  .form {
    width: 80vw;
    height: auto;

    .form-container {
      // border: 1px solid blue;
      height: 55vh;
      width: 70vw;

      .form-fields {
        // border: 1px solid red;
        width: 100%;

        input {
          width: 100%;
        }
      }
    }
  }

  /* Profile Page */
  .profile-container {
    // border: 1px solid red;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 3rem;

    .sidebar {
      // border: 1px solid blue;
      width: 90vw;
      flex-direction: row;
      justify-content: space-between;
      // margin-bottom: 2rem;
      height: 5rem;
    }

    .main {
      // border: 1px solid purple;
      padding: 2rem;
      margin-bottom: 2rem;
      width: 90vw;
      height: auto;
    }
  }
}

// Artwork Image
.art img {
